export default{
	data(){
		return{
			userName:"用户名",
			 background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: false,
			interval: 2000,
			duration: 500,
			ischeck:false,
			isLogin:false,
			isShow:false,
			avatarUrl:'',
			userInfo:{},//用户详情
			childrenList:[],//孩子信息组
			parentList:[],//家长信息组
		}
	},
	onLoad(){
		this.getIsLogin()	
	},
	onShow(){
		if(this.isLogin){
			this.getUserList()
		}
	},
	methods:{
		getIsLogin(){
			let userInfo=uni.getStorageSync("userInfo")
			if(userInfo){
				this.isLogin=true
				userInfo=JSON.parse(userInfo)
				this.userInfo=userInfo
				console.log("用户信息")
				console.log(userInfo)
			}
		},
		bindGetUserInfo(val){
			console.log("获取微信用户数据")
			console.log(val)
			let that=this
			wx.login({
				success:(e)=>{
					console.log('获取code')
					console.log(e)
					let code=e.code
					wx.getUserInfo({
						success:(res)=>{
							console.log("获取用户信息")
							// 获取头像
							console.log(res.userInfo.avatarUrl)	
							this.avatarUrl=res.userInfo.avatarUrl
							// uni.setStorageSync('userAvatar',res.userInfo.avatarUrl)
						}
					})
					that.$http.post("puparent/wxGrant",{
						type:0,//0:自己   1：别人介绍  2：主账号分享的
						code:code,
						parentId:''
					}).then(res1=>{
						if(res1.code==300){								
							uni.navigateTo({
								url:"login?avatar="+this.avatarUrl+"&openid="+res1.info
							})
						}else if(res1.code==100){
							let userInfo=""
							userInfo=JSON.stringify(res1.info)
							uni.setStorageSync('userInfo',userInfo)
							this.userInfo=res1.info
							this.isLogin=true
							uni.showToast({
								icon:"none",
								title:"登录成功！"
							})
						}
					})
				},
				fail:(err)=>{
					console.log(err)
				}
			})
		},
		getUserList(){
			//获取孩子信息，
			this.$http.post("puchildren/listChildrenLineByParentId",{
				parentId:this.userInfo.id,//家长id
				// parentId:1
			}).then(res=>{
				if(res.code==100){
					this.childrenList=res.info
				}else{
					
				}
			})
		
			// 家长组信息
			this.$http.post("puparent/getSubList",{
				// parentId:1,//家长id
				parentId:this.userInfo.id,//家长id
			}).then(res=>{
				if(res.code==100){
					this.parentList=res.info
				}
			})
		},
		lookInfo(){
			//个人资料
			if(this.isLogin){
				uni.navigateTo({
					url:"../../components/msg/userinfo?id="+this.userInfo.id
				})
			}else{
				uni.showToast({
					icon:"none",
					title:"请登录后操作！"
				})
			}
			
		},
		switch1Change(val){
			// console.log(val.detail.value)
			this.ischeck=val.detail.value
		},
		goEdit(val,id){
			if(val==1){
				//编辑孩子信息
				uni.navigateTo({
					url:"../../components/information/editchild?id="+id
				})
			}else{
				//编辑家长信息
				uni.navigateTo({
					url:"../../components/information/editparent?id="+1
				})
			}
		},
		addChild(index){
			if(index==1){
				uni.navigateTo({
					url:"../../components/information/addchildschool"
				})					
			}else{
				uni.navigateTo({
					url:"../../components/information/editparent"
				})	
			}
		},
		showDrawer(index){
			// console.log("三生三世")
			if(index==1){
				this.isShow=true
			}else{
				this.isShow=false
			}
		},	
		goInfo(index){
			switch(index){
				case 1:
					console.log("快速续费")
					uni.navigateTo({
						url:"../../components/info/renew"
					})
					break
				case 2:
					console.log("安全报告")
					break
				case 3:
					console.log("邀请奖励")
					uni.navigateTo({
						url:"../../components/share/index"
					})
					break
			}
		},
		gopay(){
			// 前去付款测试 ,正式使用时关闭
			uni.navigateTo({
				url:"../../components/information/payment"
			})
		},
	}
}
